  url('../fonts/DMSans-Regular.woff2') format('woff2');
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'DMSans';
  src: url('../fonts/DMSans-Medium.woff') format('woff'),
  url('../fonts/DMSans-Medium.woff2') format('woff2');
